import React, { useState } from "react";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import EditOutlined from "@material-ui/icons/EditOutlined";
import CheckOutlined from "@material-ui/icons/CheckOutlined";

export default function Note({ deleteItem, id, title, content }) {
  const [item, setItem] = useState({
    title,
    content,
  });
  const [edit, setEdit] = useState(false);

  const deleteNote = () => {
    deleteItem(id);
  };

  const editNote = () => {
    setEdit(true);
  };

  const closeEdit = () => {
    setEdit(false);
  };

  const editItems = ({ target: input }) => {
    const data = { ...item };
    data[input.name] = input.value;
    setItem(data);
  };

  return (
    <div className="note">
      {edit ? (
        <>
          <input
            type="text"
            name="title"
            value={item.title}
            onChange={editItems}
            placeholder="Title"
            autoComplete="off"
          />
          <textarea
            rows=""
            column=""
            name="content"
            value={item.content}
            onChange={editItems}
            placeholder="Write a note..."
            // onClick={expandIt}
          />
          <button className="btn" onClick={closeEdit}>
            <CheckOutlined className="checkIcon" style={{ color: "green" }} />
          </button>
        </>
      ) : (
        <>
          <h1>{item.title}</h1>
          <br />
          <p> {item.content} </p>
          <button className="btn" onClick={deleteNote}>
            <DeleteOutlineIcon className="deleteIcon" />
          </button>
          <button className="btn" onClick={editNote}>
            <EditOutlined className="editIcon" style={{ color: "skyblue" }} />
          </button>
        </>
      )}
    </div>
  );
}